import React, { useEffect, useState } from "react";
import InputElement from "../../formElements/InputElement";
import { Form, Formik } from "formik";
import ButtonElement from "../../formElements/ButtonElement";
import { categorySchema } from "../../../util/Schema";
import { useSelector, useDispatch } from "react-redux";
import toast, { Toaster } from "react-hot-toast";
import ListIcon from "../../formElements/ListIcon";
import { useLocation, useNavigate } from "react-router-dom";
import SelectElement from "../../formElements/SelectElement";
import {
  category,
  whole,
  action,
  createCategory,
  updateCategory,
} from "../../../redux/slices/CategorySlice";
import LineSpinner from "../../customLoader/LineSpinner";

const Category = () => {
  const [initial, setInitial] = useState({
    Category: "",
    inactive: "",
    id: "",
  });
  const [edit, setEdit] = useState(false);
  const { status, code } = useSelector(whole);
  const Category = useSelector(category);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if (code === 201 || code === 200) {
      toast.success(status);
    } else if (code === 500 || code === 404 || code === 403) {
      toast.error(status);
    }

    return () => {
      dispatch(action.cleanup());
    };
  }, [code, status]);

  useEffect(() => {
    if (location.state?.Categoryid !== undefined && Category.length > 0) {
      const identifiedCategory = Category.filter(
        (cat) => cat.id === location.state?.Categoryid
      );
      const initialState = {
        Category: identifiedCategory[0].Category,
        inactive: identifiedCategory[0].inactive,
        id: identifiedCategory[0].id,
      };
      setInitial(initialState);
      setEdit(true);
    }

    return () => {
      location.state = null;
    };
  }, [location.state]);

  const navigationHandler = () => {
    navigate("../Category/List");
  };

  return (
    <>
      {status === "loading" && <LineSpinner />}
      <Toaster />
      <div className="px-20 py-8 overflow-auto flex  justify-center h-[calc(100vh-70px)]">
        <div className="  w-full md:w-1/3 px-4 ">
          <h1 className="text-center font-semibold text-3xl py-3">Category</h1>
          <div className="w-80 py-3">
            <div
              className="float-end  text-xl text-gray-700 font-semibold cursor-pointer hover:text-orange-500"
              onClick={navigationHandler}
            >
              <ListIcon />
            </div>
          </div>

          <Formik
            initialValues={initial}
            enableReinitialize={true}
            validationSchema={categorySchema}
            onSubmit={(values, action) => {
              if (!edit) {
                dispatch(createCategory(values));
              } else {
                dispatch(updateCategory({ ...values }));
                setEdit(false);
              }

              action.setSubmitting(false);
              action.resetForm({
                values: {
                  Category: "",
                  inactive: "",
                  id: "",
                },
              });
            }}
          >
            {(props) => {
              return (
                <Form>
                  <div className=" flex flex-col  justify-center items-center  text-center mt-5 gap-3">
                    <InputElement
                      type="text"
                      placeholder="Category Name"
                      name="Category"
                    />

                    <SelectElement name="inactive">
                      <option value="">Inactive ? </option>
                      <option value="0">No</option>
                      <option value="1">Yes</option>
                    </SelectElement>

                    <ButtonElement
                      type="submit"
                      caption="SAVE"
                      disabled={props.isSubmitting}
                    />
                  </div>
                </Form>
              );
            }}
          </Formik>
        </div>
      </div>
    </>
  );
};

export default Category;
